  position: absolute;
  width: 600px;
  min-height: 350px;
  height: 25%;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.8);
  border: 3px solid rgba(204, 204, 204, 0.7);
  font-size: 12px;
  overflow-y: auto;
  margin: 0;
  list-style-type: none;
  padding: 5px;
  z-index: 100;
}

.lsp-code {
  font-family: monospace;
  background: #eee;
  border-radius: 2px;
